import "react-native-gesture-handler/jestSetup";

// Mock NativeAnimatedHelper pour supprimer les warnings Animated
jest.mock("react-native/Libraries/Animated/NativeAnimatedHelper");

// Mock ScrollView et FlatList simplement
import React from "react";
jest.mock("react-native/Libraries/Components/ScrollView/ScrollView", () => {
  const RealComponent = require("react").forwardRef(({ children }, ref) => (
    <>{children}</>
  ));
  return RealComponent;
});

jest.mock("react-native/Libraries/Lists/FlatList", () => {
  const RealComponent = require("react").forwardRef(({ data, renderItem }, ref) =>
    data.map((item, index) => renderItem({ item, index }))
  );
  return RealComponent;
});

// Supprimer les warnings Animated et RNGestureHandler
const originalError = console.error;
console.error = (...args) => {
  if (
    typeof args[0] === "string" &&
    (args[0].includes(
      "An update to Animated(View) inside a test was not wrapped in act"
    ) ||
      args[0].includes(
        "RCTBridge required dispatch_sync to load RNGestureHandlerModule"
      ))
  ) {
    return;
  }
  originalError(...args);
};
